
import { defineComponent, reactive, toRefs, nextTick } from 'vue'
import axios from 'axios'
import { debounce } from 'lodash'

import SvgIcon from '@/components/SvgIcon.vue'

export default defineComponent({
  setup() {
    const state = reactive({
      isLoading: false,
      isOpened: false,
      searchBar: null,
      results: [] as any,
      searchInput: ''
    })

    const onInput = debounce(() => {
      if (state.searchInput.length < 3) {
        return
      } else {
        state.isLoading = true
        const fetchItems = async () => {
          const response = await axios(
            `https://api.rawg.io/api/games?key=c0233312e0954f0ca72e3d1e9d64ef32&search=${state.searchInput}`
          )
          state.results = []
          state.results.push(response.data.results)
          console.log(state.results)
          state.isLoading = false
        }
        fetchItems()
      }
    }, 500)

    const reloadPage = () => {
      window.location.reload()
    }

    const handleClick = () => {
      state.isOpened = !state.isOpened
      if (state.isOpened) {
        nextTick(() => {
          ;((state.searchBar as unknown) as HTMLElement).focus()
        })
      }
    }

    return {
      ...toRefs(state),
      handleClick,
      onInput,
      reloadPage
    }
  },
  components: {
    SvgIcon
  }
})
