
import { defineComponent, toRefs, reactive, onMounted } from 'vue'

import { truncate } from 'lodash'

export default defineComponent({
  setup(context) {
    const state = reactive({
      isHovering: false,
      isNegative: false,
      isMixed: false,
      isPositive: false
    })

    onMounted(() => {
      if (context?.result?.metacritic <= 45) {
        state.isNegative = true
        state.isMixed = false
        state.isPositive = false
      } else if (context?.result?.metacritic < 80) {
        state.isNegative = false
        state.isMixed = true
        state.isPositive = false
      } else {
        state.isNegative = false
        state.isMixed = false
        state.isPositive = true
      }
    })

    function shorterName(name) {
      return truncate(name, { length: 32, omission: '..' })
    }

    function requestCrop(uri) {
      if (uri === null) return
      else {
        const crop = uri.replace("/media/", "/media/crop/600/400/")
        return crop
      }
    }

    return {
      ...toRefs(state),
      shorterName,
      requestCrop
    }
  },
  props: {
    result: {
      type: Object
    }
  }
})
