
import { defineComponent, reactive, toRefs } from 'vue'
import SvgIcon from '@/components/SvgIcon.vue'

export default defineComponent({
  setup() {
    const state = reactive({
      isOpened: false,
      opacity: 'opacity-0'
    })

    const handleClick = () => {
      state.isOpened = !state.isOpened
    }

    return { ...toRefs(state), handleClick }
  },
  components: {
    SvgIcon
  }
})
